.App {
  margin:0 auto;
  width:100%;
  text-align: center;
  padding:0;
  display:flex;
  flex-direction: column;
  align-items: center;
  background:#f6f6f6;
}

.guides-wrapper{
  display:flex;
  flex-direction:column;
  align-items:center;
  background-color:#e3f6f5;
  padding-bottom:4rem;
}

.guides-wrapper > div:last-of-type{
  margin-bottom:6rem;
}

.guides-wrapper > h2{
  margin-top:10rem;
  margin-bottom:2rem;
  font-size: 1.6rem;
}

.big{
  margin:0 auto !important;
  display:block;
  width:80%;
}

.App-Clients{
  margin:0 auto;
  width:100%;
  max-width:600px;
  min-height:100vh;
  text-align: center;
  padding:0;
  display:flex;
  flex-direction: column;
  align-items: center;
  background:#f6f6f6;

}

.App-Guides{
  margin:0 auto;
  width:100%;
  min-height: 100vh;
  text-align: center;
  padding:0;
  display:flex;
  flex-direction: column;
  align-items: center;
  background:#f6f6f6;
} 

.google-test{
  width:100%
}

.gmarker{
  width:20px;
  height:20px;
  border-radius:50%;
  background-color:#ffd803;
  border: 2px solid #272343;
}